<template>
	<div class="Secure">
		<div class="se-nav">
			<van-nav-bar title="安全中心" left-text="返回" right-text="退出登陆" left-arrow @click-left="back" @click-right="logout" />
		</div>

		<div class="se-box">
			<van-cell title="修改密码" is-link @click="showPopup" />
			<van-cell title="注销账号" is-link @click="logoff" />
		</div>
		<van-action-sheet v-model="show" title="修改密码" style="font-weight: bold;">
			<div class="content">
				<van-cell-group>
					<van-field v-for="(item, index) in passwords" :key="index" :type="item.isPassword ? 'password' : 'text'"
					 placeholder="密码首字母开头6到16位" v-model="item.password" :right-icon="item.isPassword ? 'closed-eye' : 'eye-o'"
					 @click-right-icon="viewPassword(item)" :label="item.title" />
					<van-button block type="info" class="editpwd" native-type="submit" color="#212572" @click="confirmPassword">确认修改密码</van-button>
				</van-cell-group>
			</div>
		</van-action-sheet>
	</div>
</template>

<script>
	import '../assets/less/secure.less'
	import {
		utils
	} from '../assets/js/utils'
	import {
		validForm
	} from '../assets/js/ValidForm'

	export default {
		data() {
			return {
				show: false,
				passwords: {
					old: {
						password: '',
						isPassword: true,
						title: '旧密码'
					},
					new: {
						password: '',
						isPassword: true,
						title: '新密码'
					}
				},

			}
		},
		created() {

		},

		methods: {

			//上一级
			back() {
				this.$router.go(-1);
			},

			showPopup() {
				this.show = true;
			},

			// 查看密码
			viewPassword(item) {
				item.isPassword = !item.isPassword
			},

			//退出登陆
			logout() {
				this.$dialog.confirm({
					title: '警告',
					message: '你确定要退出当前账号'
				}).then(() => {
					this.$toast.loading({
						message: '加载中...',
						//防止穿透
						forbidClick: true,
						duration: 0,
						loadingType: 'spinner',
					})
					localStorage.removeItem('RETK')
					setTimeout(() => {
						this.$router.push({
							name: 'Login'
						})
						this.$toast.clear();
					}, 1000)
				})
			},

			//确认修改密码
			confirmPassword() {

				//检查是否有token
				let tokenString = localStorage.getItem('RETK');
				if (!tokenString) {
					return
				}

				let o = {
					oldPassword: {
						value: this.passwords.old.password,
						reg: /^[A-Za-z]\w{5,15}$/,
						errorMsg: '密码首字母开头6到16位'
					},
					password: {
						value: this.passwords.old.password,
						reg: /^[A-Za-z]\w{5,15}$/,
						errorMsg: '密码首字母开头6到16位'
					},
				}

				//验证表单
				if (!validForm.valid(o)) {
					//验证不通过
					return;
				}

				//发起修改密码请求

				let data = utils.queryString({
					appkey: this.appkey,
					tokenString,
					oldPassword: this.passwords.old.password,
					password: this.passwords.new.password
				});


				this.axios({
					method: 'POST',
					//请求参数
					url: '/updatePassword',
					data
				}).then(result => {
					//关闭提示加载
					this.$toast.clear();
					if (result.data.code == "E001") {
						localStorage.removeItem('RETK');
						this.$router.push({
							name: 'Login'
						})
					} else {
						this.$notify({
							type: 'warning',
							message: result.data.msg
						})
					}
				}).catch(err => {
					this.$toast.clear();
				})
			},


			//注销账号
			logoff() {
				this.$dialog.confirm({
					title: '警告',
					message: '你确定要注销当前账号,一旦确认将无法恢复!'
				}).then(() => {
					有token
					let tokenString = localStorage.getItem('RETK');
					if (!tokenString) {
						return
					}

					let data = utils.queryString({
						appkey: this.appkey,
						tokenString,
					});

					this.axios({
						method: 'POST',
						//请求参数
						url: '/destroyAccount',
						data
					}).then(result => {
						//关闭提示加载
						this.$toast.clear();
						if (result.data.code == "G001") {
							this.$toast(result.data.msg);
							localStorage.removeItem('RETK');
							this.$router.push({
								name: 'Login'
							})
						} else {
							this.$notify({
								type: 'warning',
								message: result.data.msg
							})
						}
					}).catch(err => {
						this.$toast.clear();
					});

				}).catch(() => {
					this.$toast.clear();
				});
			},
		},
	};
</script>

<style>
</style>
